@import url(https://fonts.googleapis.com/css?family=Lato:400,700,700i,900,900i);
.header {
  background-color: #1d1e2b;
  display: flex;
  justify-content: space-around;
  padding: 20px 0;
  color: #babcc5;
}
.header svg {
  width: 38px;
}
.header ul {
  display: flex;
  justify-content: center;
  list-style-type: none;
  margin-left: 300px;
}
.header ul li:not(:first-of-type) {
  border-left: 1px solid #babcc5;
}
.header ul li {
  padding: 0 20px;
}
html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Lato, sans-serif;
}

#root {
  min-height: 100%;
}

.logo-path {
  fill: #babcc5;
}

.social-media-icons {
  display: flex;
  align-items: center;
}
.social-media-icons .social-media-icon {
  margin: 0 5px;
  height: 28px;
}
.social-media-icons .social-media-email {
  margin-top: 6px;
}
